html {
    height: 100%;
    background: #171717;
    color:#c1c1c1;
}

html, body {
    margin:0;
}

body {
    min-height: 100%;
}

a {
    color: #15829e;
}

.wrapper{
    padding-top: 80px;
    display:flex;
    flex-direction:row;
    .content{
        width:100%;
        padding: 15px;
        padding-left:265px;
    }
}

::selection {
    text-shadow: 0 0 4px #1b829e;
    color: #55d1f3;
    background:black;
}

::-moz-selection{
    text-shadow: 0 0 4px #1b829e;
    background:black;
    color: #55d1f3;
}


::-webkit-scrollbar-track
{
    background-color: #171717;
}

::-webkit-scrollbar
{
    width: 12px;
    background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb
{
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #1b829e;
}
